.header {
    background: var(--navbar-background-color);
    z-index: 10;
    position: fixed;
    top: 0;
    width: 100vw;
    height: 5rem;
}

.header::after {
    content: '';
    display: table;
    clear: both;
}

.title {
    font-size: 3rem;
    color: var(--text_color);
    position: absolute;
}

.anchor {
    font-size: 2rem;
    text-decoration: none;
    text-transform: uppercase;
    color: var(--text_color);
}

.anchor::before {
    content: '';
    display: block;
    height: 5px;
    background-color: var(--text_color);;

    position: absolute;
    top: 0;
    width: 0%;

    transition: all ease-in-out 250ms;
}

.anchor:hover {
    color: var(--selected_text_color);
}

.anchor:hover::before {
    width: 100%;
}

.anchor-list-item {
    display: inline-block;
    margin-left: 3vw;
    margin-right: 3vw;
    padding-top: 1rem;
    padding-bottom: 1rem;
    position: relative;
}

.nav-element {
    float: right;
}

.anchor-list {
    list-style: none;
    margin: 0;
    padding: 0;
}